*{
  box-sizing: border-box;
}
body{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center; 
  /*by horizontally*/
  align-items: center; 
  /*by vertically*/
  background-color: #0e80b0;
}
.btn{
  background-color: white;
  color:#eddc15;
  font-family:sans-serif;
  margin: 15px;
  padding: 5px;
  border: 2px solid #e5dc15;
  border-radius: 5px;
  box-shadow: 0 12px #c2bb11, 0 18px rgba(0,0,0,0.4);
  font-size: 60px;
  width:90px;
  height:80px;
  cursor: pointer;
  position: relative;
  top:0;

}
.btn:hover{
  background-color: #e5dc15;
  color:#0e80b0;
  top:4px;
  box-shadow: 0 8px #c2bb11, 0 14px rgba(0,0,0,0.4);


}
.disabled .btn{
  cursor: default;
}
.disabled .btn:hover{
  top:0;
  background: #ffffff;
  color: #e5dc15;
  box-shadow: 0 12px #c2bb11,0 18px rgba(0,0,0,0.4);
}
.btn:active{
  top:8px;
  box-shadow: 0 4px #c2bb11, 0 10px rgba(0,0,0,0.4);

}
.board >div{
  display: flex;
}

.game{
  display: flex;
  flex-wrap: wrap;
}

.game-info{
  margin: 15px;
  color: #ffffff;
}